import { Component, OnInit } from '@angular/core';
import { Router, RouterEvent, NavigationEnd } from '@angular/router';
import { LoadingController,AlertController,ModalController,NavController, Platform } from '@ionic/angular';
import {AuthenticationService} from '../../services/authentication/authentication.service';
import { InAppBrowser } from '@ionic-native/in-app-browser/ngx';
import {ProfileService} from '../../services/profile/profile.service';
import {OfflineService} from '../../services/offline/offline.service';
import {SubmissionService} from '../../services/submission/submission.service';
import * as moment from 'moment';

@Component({
  selector: 'app-settings',
  templateUrl: './settings.page.html',
  styleUrls: ['./settings.page.scss'],
})
export class SettingsPage implements OnInit {

    public user:any;
    public paymentTotal:number;
    public loading:any;
    
    constructor(public plt: Platform, private profileService: ProfileService,
        private authenticationService: AuthenticationService, private alertCtrl: AlertController, private iab: InAppBrowser, 
        private offlineService: OfflineService, private submissionService:SubmissionService, private loadingController: LoadingController) {
        
        this.user = {};
        this.authenticationService.getUserData().subscribe((value) => {
            this.user = value;
            
        });
        
        this.paymentTotal = 0;
        
        
    }
    
    ngOnInit() {
    }  
    
    ionViewDidEnter(){
    }
    
    
    public syncData(){

        this.loadingController.create({
          message: 'Your submissions are being synced. Please wait...',
          duration: 20000
        }).then((el) => {
  
            this.loading = el;
            el.present();
        });         
        
        
        this.offlineService.getSubmissions().then((data) => {
            if (data){
                
                for (let submission of data){
                    
                    this.submissionService.createSubmission(submission.fields, submission.form_id).then(() => {
                        
                    })
                    
                }
                

                
                
                //test internet
                this.submissionService.getSubmissions("").then(() => {
                
                    this.showSuccess(); 
                    
                    setTimeout(() => {
                        this.offlineService.saveSubmissions([]);
                    },5000);                    
                    
                    
                 
                }).catch(() => {
                    this.showError();                    
                });              
                
                
                
            }
            
            else{
                this.showSuccess();
            }
        }).catch(() => {
        
            this.showError();
        })       
    }
    
    
    
    private showSuccess(){
        setTimeout(() => {
            this.loading.dismiss();

            let alert = this.alertCtrl.create({
                 header: 'Success',
                 message: 'Your submissions have been synced',
                 buttons: [
                     {
                         text: 'Dismiss',
                         role: 'cancel',
                         handler: data => {
                         }
                     }                              
                 ]           
             }).then((el) => {
                 el.present();
             });   
        },1000);      
    }
    
    
    private showError(){
        setTimeout(() => {
            this.loading.dismiss();

             let alert = this.alertCtrl.create({
                  header: 'Error',
                  message: 'There was an error syncing your submissions. Please try again.',
                  buttons: [
                      {
                          text: 'Dismiss',
                          role: 'cancel',
                          handler: data => {
                          }
                      }                              
                  ]           
              }).then((el) => {
                  el.present();
              });    
        },1000);          
    }


    
    public openChangePassword(){
        let alert = this.alertCtrl.create({
            header: 'Change Password',
            inputs: [           
                {
                    name: 'password',
                    placeholder: 'Password',
                    type: 'password'
                },
                {
                    name: 'confirmPassword',
                    placeholder: 'Confirm Password',
                    type: 'password'
                }
            ],
            buttons: [
                {
                    text: 'Cancel',
                    role: 'cancel',
                    handler: data => {
                    }
                },
                {
                    text: 'Change',
                    handler: data => {
                        
                        if (data.password && data.confirmPassword && data.password === data.confirmPassword){

                            
                            this.authenticationService.changePassword(this.user.email, data.password, data.confirmPassword).then(() => {
                                let alert = this.alertCtrl.create({
                                    header: 'Success',
                                    message: 'Your password has been updated',
                                    buttons: [
                                        {
                                            text: 'Dismiss',
                                            role: 'cancel',
                                            handler: data => {
                                            }
                                        }                              
                                    ]           
                                }).then((el) => {
                                    el.present();
                                });                                 
                            }).catch(() => {
                                let alert = this.alertCtrl.create({
                                    header: 'Error',
                                    message: 'There was an error while changing your password.',
                                    buttons: [
                                        {
                                            text: 'Cancel',
                                            role: 'cancel',
                                            handler: data => {
                                            }
                                        },
                                         {
                                            text: 'Try Again',
                                            handler: data => {
                                                setTimeout(() => {this.openChangePassword()},200);
                                            }
                                        }                               
                                    ]            
                                }).then((el) => {
                                    el.present();
                                });                                
                            });
                        }
                        
                        else{
                            let alert = this.alertCtrl.create({
                                header: 'Error',
                                message: 'Passwords do not match',
                                buttons: [
                                    {
                                        text: 'Cancel',
                                        role: 'cancel',
                                        handler: data => {
                                        }
                                    },
                                     {
                                        text: 'Try Again',
                                        handler: data => {
                                            setTimeout(() => {this.openChangePassword()},200);
                                        }
                                    }                               
                                ]            
                            }).then((el) => {
                                    el.present();
                                });                              
                        }
    
                    }
                }
                
            ]
        }).then((el) => {
            el.present();
        });          
    }
   
    
    
    public logout(){
        this.authenticationService.logout();
      
    }      
}